<template>
  <div>
    <p>Cadastro de Banco</p>
    <div>
      <table class="tableBank">
        <thead>
          <th>Id</th>
          <th>Código</th>
          <th>Descrição</th>
        </thead>
        <tbody>
          <tr v-for="(bank, index) in banks" :key="index">
            <td>{{ bank.id }}</td>
            <td>{{ bank.code }}</td>
            <td>{{ bank.description }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      banks: [],
    };
  },
  props: {},
  created() {
    this.$http
      .get("http://localhost:6001/bank/all")
      .then((res) => res.json())
      .then((result) => (this.banks = result));
  },
  methods: {},
};
</script>

