import Vue from 'vue';
import Router from 'vue-router';
import Bank from './components/Bank/Bank'
import About from './components/About/About'

Vue.use(Router);


export default new Router({
    routes: [
        {
            path: '/',
            nome: 'Bank',
            component: Bank
        },
        {
            path: '/bank',
            nome: 'Banco',
            component: Bank
        },
        {
            path: '/about',
            nome: 'Sobre',
            component: About
        }
        // {
        //     path: '/alunoDetalhe/:id',
        //     nome: 'AlunoDetalhe',
        //     component: AlunoDetalhe
        // },
    ]
})